.row {
    margin: 0 -12px;

    &::after {
        content: '';
        clear: both;
        display: table;
    }
}

.col-1,.col-2,.col-3,.col-4,.col-5,.col-6,.col-7,.col-8,.col-9,.col-10,.col-11,.col-12 {
    position: relative;
    min-height: 1px;
    padding: 0 12px;

    span.guide {
        display: block;
        height: 40px;
        background-color: #000;
        text-align: center;
        line-height: 2.5;
        color: #fff;
        border-radius: 5px;
        margin-bottom: 24px;
    }
}


@include pc {
    .col-1,.col-2,.col-3,.col-4,.col-5,.col-6,.col-7,.col-8,.col-9,.col-10,.col-11,.col-12 {
        float:left;
    }

    .col-12 { width: 100%}
    .col-11 { width: 91.66666667%}
    .col-10 { width: 83.33333333%}
    .col-9 { width: 75%}
    .col-8 { width: 66.66666667%}
    .col-7 { width: 58.33333333%} 
    .col-6 { width: 50%}
    .col-5 { width: 41.66666667%}
    .col-4 { width: 33.33333333%}
    .col-3 { width: 25%}
    .col-2 { width: 16.66666667%}
    .col-1 { width: 8.33333333%}
}

@include tablet {
    
}

@include mobile {
    .col-1,.col-2,.col-3,.col-4,.col-5,.col-6,.col-7,.col-8,.col-9,.col-10,.col-11,.col-12 {
        margin-bottom: 10px;
    }
}